import React, { Component, useState, useEffect } from "react";
import { useParams, withRouter } from "react-router-dom";
import { Container } from "@material-ui/core";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import Footer from "./Footer/Footer";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Grid } from "@material-ui/core";
import { Control, LocalForm } from "react-redux-form";
import CloseIcon from "@material-ui/icons/Close";
import Paper from "@material-ui/core/Paper";
import InputBase from "@material-ui/core/InputBase";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import "./Forum/Forum.css";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import EventIcon from "@material-ui/icons/Event";
// import classicEditor from "@ckeditor/ckeditor5-build-classic";
// import { CKEditor } from "@ckeditor/ckeditor5-react";
import CustomLoader from "./Loader";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
  hover: {
    "&:hover": {
      backgroundColor: "#f7f7f7",
    },
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  cardroot: {
    maxWidth: 50,
  },
  overlay: {
    position: "absolute",
    top: 0,
    left: 0,
    backgroundColor: "white",
    opacity: 0.2,
    color: "black",
    height: "100%",
    width: "100%",
  },
  n_overlay: {
    display: "none",
  },
  content: {
    overflowX: "hidden",
  },
}));

function Problems({ course, module, threads }) {
  const classes = useStyles();
  let thrds = null;
  if (threads.length > 0) {
    thrds = threads.map((thread, i) => {
      return (
        <Link
          to={`/forum/${course}/${module}/${thread.id}`}
          style={{ textDecoration: "none", color: "black" }}
        >
          <ListItem className={classes.hover}>
            <ListItemText
              primary={thread.title}
              secondary={`By ${thread.author}`}
            />
          </ListItem>
        </Link>
      );
    });
  } else {
    return <div>No Match</div>;
  }
  return <List className={classes.root}>{thrds}</List>;
}

function StartThread({
  open,
  handleClose,
  postQuestion,
  slots,
  files,
  setFiles,
}) {
  const classes = useStyles();

  const [live, setLive] = useState(false);
  const [selected, setSlot] = useState(null);

  const getFile = (e) => {
    setFiles(e.target.files, false);
  };

  const handleSubmit = (values, file) => {
    setLive(false);
    handleClose();
    postQuestion(values, file, selected);
  };

  const Close = () => {
    setFiles([], true);
    setLive(false);
    handleClose();
  };

  return (
    <Dialog
      className="editModal"
      fullWidth
      open={open}
      onClose={Close}
      scroll="body"
      aria-labelledby="scroll-dialog-title"
      aria-describedby="scroll-dialog-description"
    >
      <div className="d-flex flex-row">
        <DialogTitle id="scroll-dialog-title">Ask Question</DialogTitle>
        <div className="ml-auto">
          <DialogActions>
            <Button style={{ backgroundColor: "white", outline: "none" }}>
              <CloseIcon
                onClick={Close}
                style={{ backgroundColor: "white", margin: 10 }}
              />
            </Button>
          </DialogActions>
        </div>
      </div>
      <DialogContent dividers={true} className={classes.content}>
        <LocalForm onSubmit={(values) => handleSubmit(values, files)}>
          <Grid row style={{ marginBottom: 15 }}>
            <label htmlFor="question">Question</label>
            <Control
              type="text"
              model=".question"
              id="question"
              name="question"
              placeholder="question"
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid row style={{ marginBottom: 15 }}>
            <label htmlFor="description">Description</label>
            <Control
              component="textarea"
              model=".description"
              id="description"
              name="description"
              placeholder="description"
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid row>
            <FormControlLabel
              control={
                <Checkbox
                  onChange={(event) => {
                    setLive(event.target.checked);
                  }}
                  name="live"
                />
              }
              label="Discuss Live"
            />
          </Grid>
          {live ? (
            <Grid row style={{ marginBottom: 15 }}>
              <div>
                {slots.length > 0 ? (
                  <div className="row justify-content-center">
                    {slots.map((slot, i) => {
                      if (slot.is_available) {
                        return (
                          <Card
                            key={i}
                            onClick={() => {
                              setSlot(slot.id);
                            }}
                            style={{
                              margin: 5,
                              // border: "10px #f50057",
                              width: 170,
                              outline: "none",
                              background: "rgba(0, 0, 0, 0.54)",
                              color: "white",
                            }}
                          >
                            <CardActionArea style={{ outline: "none" }}>
                              <CardContent>
                                <div className="row justify-content-center">
                                  <EventIcon />
                                </div>
                                <Typography align="center">
                                  {new Date(slot.starts_at).toDateString()},
                                </Typography>
                                <Typography align="center">
                                  {new Date(slot.starts_at).toTimeString()}
                                </Typography>
                              </CardContent>
                              <div
                                className={
                                  selected === slot.id
                                    ? classes.overlay
                                    : classes.n_overlay
                                }
                              ></div>
                            </CardActionArea>
                          </Card>
                        );
                      } else {
                        return null;
                      }
                    })}
                  </div>
                ) : null}
              </div>
            </Grid>
          ) : null}
          <Grid row style={{ marginBottom: 15 }}>
            <Button
              variant="contained"
              component="label"
              color="secondary"
              fullWidth
            >
              Attachments &nbsp;
              <input
                type="file"
                id="files"
                name="files"
                multiple
                onChange={getFile}
                accept="image/*"
                style={{ outline: "none", display: "none" }}
              />
            </Button>
            {Object.values(files).map((file) => {
              return <div>{file.name}</div>;
            })}
          </Grid>
          <Grid row style={{ marginBottom: 15 }}>
            <Button
              variant="contained"
              type="submit"
              fullWidth={true}
              style={{
                backgroundColor: "#09bfad",
                color: "white",
                outline: "none",
              }}
            >
              Post
            </Button>
          </Grid>
        </LocalForm>
      </DialogContent>
    </Dialog>
  );
}

class Threads extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      search: null,
      files: [],
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.postQuestion = this.postQuestion.bind(this);
    this.setFiles = this.setFiles.bind(this);
    this.successCreate = this.successCreate.bind(this);
  }

  toggleModal() {
    this.setState({
      open: !this.state.open,
    });
  }

  postQuestion(values, files, selected) {
    this.props.createThread(
      this.props.forum_id,
      this.props.module_id,
      this.props.module,
      values.question,
      values.description,
      files,
      null,
      selected,
      this.successCreate
    );
  }

  successCreate(id) {
    this.props.history.push(
      `/forum/${this.props.course}/${this.props.module}/${id}`
    );
  }

  setFiles(files, close) {
    if (close) {
      this.setState({
        files: [],
      });
    } else {
      let prevFiles = this.state.files;
      let arr = [];
      let arr1 = [];
      let arr2 = [];
      for (let i = 0; i < files.length; ++i) {
        for (let j = 0; j < prevFiles.length; ++j) {
          if (files[i].name === prevFiles[j].name) {
            arr.push(i);
          }
        }
      }
      arr1 = Object.values(files);
      for (let i = 0; i < arr.length; ++i) {
        arr1.splice(arr[i], 1, null);
      }
      arr1 = arr1.filter((a) => a !== null);
      arr2 = Object.values(prevFiles);
      arr2.pop();
      arr1 = arr2.concat(arr1);
      let finalObj = { ...arr1 };
      finalObj.length = Object.keys(finalObj).length;
      this.setState({
        files: finalObj,
      });
    }
  }

  render() {
    return (
      <>
        <Container className="forum" maxWidth="lg" style={{ marginBottom: 40 }}>
          <div style={{ display: "flex", flex: "row" }}>
            <div>
              <h2>{this.props.module}</h2>
            </div>
            <div style={{ marginLeft: "auto", display: "flex", flex: "row" }}>
              <Paper component="form" style={{ marginRight: 5 }}>
                <InputBase
                  style={{ marginLeft: 10 }}
                  placeholder="Search"
                  onChange={(val) => {
                    this.setState({
                      search: val.target.value,
                    });
                  }}
                  inputProps={{ "aria-label": "search google maps" }}
                />
                <IconButton type="submit" aria-label="search">
                  <SearchIcon />
                </IconButton>
              </Paper>
              <Button
                variant="contained"
                style={{
                  backgroundColor: "#09bfad",
                  color: "white",
                  outline: "none",
                }}
                onClick={() => {
                  this.props.fetchSlots(this.props.course);
                  this.toggleModal();
                }}
              >
                Ask Question
              </Button>
              <StartThread
                slots={this.props.slots}
                open={this.state.open}
                handleClose={this.toggleModal}
                postQuestion={this.postQuestion}
                setFiles={this.setFiles}
                files={this.state.files}
              />
            </div>
          </div>
          <hr />
          {this.props.threads.length > 0 ? (
            <Problems
              threads={this.props.threads.filter((thread) => {
                if (this.state.search === null) {
                  return thread;
                } else if (
                  thread.title
                    .toLowerCase()
                    .includes(this.state.search.toLowerCase())
                ) {
                  return thread;
                } else {
                  return null;
                }
              })}
              module={this.props.module}
              course={this.props.course}
            />
          ) : (
            <div>
              No questions yet, click the above button to add your question
            </div>
          )}
        </Container>
        <Footer />
      </>
    );
  }
}

function ThreadList(props) {
  let { course, forum_id, module, module_id } = useParams();

  const success = () => {};

  const failed = () => {
    props.history.push("/home");
  };

  useEffect(() => {
    props.fetchThreads(module_id, success, failed);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (props.forum.threads.length >= 0) {
    return (
      <Threads
        course={course}
        forum_id={forum_id}
        module={module}
        module_id={module_id}
        slots={props.forum.slots}
        threads={props.forum.threads}
        createThread={props.createThread}
        fetchSlots={props.fetchSlots}
        history={props.history}
      />
    );
  } else {
    return (
      <div className="loader">
        <CustomLoader />
      </div>
    );
  }
}

export default withRouter(ThreadList);
